.Header
{
    display: flex;
    justify-content: space-between;
    background-color: #000000;
    padding-top: 2vh;
    padding-bottom: 2vh;
    color: white;
    font-size: 24px;
    font-weight: 400;
}
.Header>div
{
    margin-left: 15px;
}
.options span
{
    padding-inline: 10px;
}
.options span a
{
    text-decoration: none;
    color: white;
}
.Heading
{
    font-size: 50px;
    font-weight: 700;
    text-align: center;
}
.productinfo
{
    display: grid;
    font-weight: 600;
    font-size: 20px;
}
.card
{
  margin: 10px;
  padding: 10px;
  width: 30.8vw; /* Adjust the width as needed for your design */
  border: 2px solid #000000;
}
.ImageP
{
    width: 30vw;
    height: 30vh;
}
.cards-c
{
    display: flex;
  }
.add 
{
    text-decoration: none;
    border: 1px solid #000000;
    padding: 0.3vh 12vw;
    background-color: #000000;
    color: white;
}